div.MimotoCMS_pages_actions_ActionOverview_Action {

    flex: auto;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;

    padding: 15px 15px 20px 20px;
    background-color: #ffffff;
    min-width: 300px;
}

div.MimotoCMS_pages_actions_ActionOverview_Action-info {
    flex: auto;
    text-align: left;
}

    div.MimotoCMS_pages_actions_ActionOverview_Action-head {
        margin-bottom: 10px;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-row {
        margin-bottom: 15px;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-section {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: flex-start;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-section-label {

        flex: none;

        display: inline-block;
        margin-right: 10px;
        margin-top: 9px;
        min-width: 50px;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-section-data {
        flex: auto;
        display: inline-block;
        padding: 5px;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-namecontainer {
        display: inline-block;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-name, div.MimotoCMS_pages_actions_ActionOverview_Action-name p {
        font-size: 20px;
        font-weight: bold;
        color: $tower-gray;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-file {
        font-style: italic;
        font-weight: normal;
        color: $pale-slate;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-settings-empty {
        padding: 10px 20px 15px 0;
        font-style: italic;
        color: #858585;
    }

    div.MimotoCMS_pages_actions_ActionOverview_Action-settings {
        padding: 0;
    }

div.MimotoCMS_pages_actions_ActionOverview_Action-menu {
    float: right;

    & > .MimotoCMS_pages_actions_ActionOverview_Action-menuitem {
        display: inline-block;
        margin-top: 4px;
    }
}

div.MimotoCMS_modules_ListItem-sorthandlecontainer {
    display: inline-block;
    float: right;
    padding-top: 7px;
    padding-left: 10px;
}


div.MimotoCMS_pages_actions_ActionOverview_Action-conditionals {

    padding: 0;

    & > div {
        margin-bottom: 5px;
    }

    & > div:last-child {
        margin-bottom: 0;
    }
}

div.MimotoCMS_pages_actions_ActionOverview_Action-conditionals-empty {
    //display: inline-block;
    font-style: italic;
    color: #858585;
}

span.MimotoCMS_pages_actions_ActionOverview_Action-conditionals-addFirst {
    cursor: pointer;
    text-decoration: underline;
}

div.MimotoCMS_pages_actions_ActionOverview_Action-conditionals-add {
    display: inline-block;
    font-style: italic;
    color: #858585;
    cursor: pointer;
    text-decoration: underline;
    margin-left: 70px;
}


div.MimotoCMS_pages_actions_ActionOverview_Action-conditionals-section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;

    & > div {
        flex: none;
        display: inline-block;
        margin-right: 8px;

        &:last-child {
            margin-right: 10px;
        }
    }
}


div.MimotoCMS_pages_actions_ActionOverview_Action-settings {

    padding: 0;

    & > div {
        margin-bottom: 5px;
    }

    & > div:last-child {
        margin-bottom: 0;
    }
}

div.MimotoCMS_pages_actions_ActionOverview_Action-settings-empty {
    //display: inline-block;
    font-style: italic;
    color: #858585;
}

span.MimotoCMS_pages_actions_ActionOverview_Action-settings-addFirst {
    cursor: pointer;
    text-decoration: underline;
}

div.MimotoCMS_pages_actions_ActionOverview_Action-settings-add {
    display: inline-block;
    font-style: italic;
    color: #858585;
    cursor: pointer;
    text-decoration: underline;
    margin-left: 70px;
}


div.MimotoCMS_pages_actions_ActionOverview_Action-settings-section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;

    & > div {
        flex: none;
        display: inline-block;
        margin-right: 8px;

        &:last-child {
            margin-right: 10px;
        }
    }
}



